* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

html, body {
  background-color: lightgrey;
  width: 100%;
  height: 100%;
}

body {
    font-family: sans-serif;
    overflow: hidden;
}

#root {
  width: 100%;
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto 1fr 1fr 1fr auto;
  grid-gap: 10px;
  padding: 10px;
  /*border: 3px solid white;*/
}

header, footer {
  grid-column: 1 / span 2;
}

header {
  text-align: center;
}

footer {
  font-size: xx-small;
}

span {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

button {
  padding-left: 0.75em;
  padding-right: 0.75em;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  color: white;
  font-size: xx-large;
  border-radius: 4px;
  background-color: #008CBA;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.floatLeft {
  float: left;
}

.floatRight {
  float: right;
}

.tile {
  position: relative;
}

.tile > img {
  max-width: 100%;
  max-height: 100%;

  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

  pointer-events: none;
}

.img_text {
    position: absolute;
    top: 90%;
    left: 0;
    right: 0;
    -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
    text-align: center;
    font-size: x-large;
    font-weight: 500;
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

#audio_control {
  display: inline;
}

#audio_control > button {
  font-size: x-small;
}

@media (orientation: portrait) {

}

@media (orientation: landscape) {

  .container {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto 1fr 1fr auto;
  }

  header, footer {
    grid-column: 1 / span 3;
  }

  .img_text {
    top: 80%;
  }
}



